



































































import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			address: {
				realName: "",
				phone: "",
				province: "",
				city: "",
				district: "",
				detail: "",
				postCode: "",
				default: null
			},
			editType: 'edit',
			isDefault: false,
			cityPickerValue: [0, 0, 1],
			themeColor: '#ffc107'
		};
	},
	methods: {
		onCancel(e) {
			console.log(e)
		},
		chooseCity() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			this.region = e;
			console.log(e)
			let labels = e.label.split("-")
			this.address.province = labels[0]
			this.address.city = labels[1]
			this.address.district = labels[2]
		},
		isDefaultChange(e) {
			this.address.default = e.detail.value;
		},
		del() {
			let that = this
			uni.showModal({
				title: '',
				content: '',
				success: (res) => {
					if (res.confirm) {
						that.deleteAddress()
					} else if (res.cancel) {
						console.log('');
					}
				}
			});

		},
		save() {
			if (this.editType != 'edit') {
				// ID
				this.address.id = null
			}
			if (!this.address.realName) {
				uni.showToast({
					title: '',
					icon: 'none'
				});
				return;
			}
			if (!this.address.phone) {
				uni.showToast({
					title: '',
					icon: 'none'
				});
				return;
			}
			if (!this.address.detail) {
				uni.showToast({
					title: '',
					icon: 'none'
				});
				return;
			}
			if (this.address.province == '' || this.address.city == '' || this.address.district == '') {
				uni.showToast({
					title: '()',
					icon: 'none'
				});
				return;
			}
			uni.showLoading({
				title: '',
				mask: true
			})
			// ajax,
			this.saveAddress()
		},
		saveAddress() {
			let that = this
			this.$pin.request(this.editType == 'edit' ? 'PUT' : 'POST', '/commons/user/address', this.address,
				successData => {
					uni.hideLoading()
					uni.showToast({
						icon: 'success',
						title: ''
					})
					uni.navigateBack(-1)
				},
				failData => {
					uni.hideLoading()
					uni.showToast({
						icon: 'none',
						title: ''
					})
				}
			)
		},
		deleteAddress() {
			uni.showLoading({
				title: '',
				mask: true
			})
			let that = this
			this.$pin.request('DELETE', '/commons/user/address', this.address,
				successData => {
					uni.hideLoading()
					uni.showToast({
						icon: 'success',
						title: ''
					})
					uni.navigateBack(-1)
				},
				failData => {
					uni.hideLoading()
					uni.showToast({
						icon: 'none',
						title: ''
					})
				}
			)
		}
	},
	onLoad(e) {
		//
		this.editType = e.type;
		if (e.type == 'edit') {
			uni.getStorage({
				key: 'address',
				success: (e) => {
					this.address = e.data
					this.cityPickerValue[0] = e.data.province;
					this.cityPickerValue[1] = e.data.city;
					this.cityPickerValue[2] = e.data.district;
				}
			})
		}

	},
	onBackPress() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel();
			return true;
		}
	},
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	}
};
